.content {
  margin-top: 9.375rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  .content {
    margin-top: 11.875rem;
  }
}
.content::before {
  content: "";
  position: absolute;
  z-index: -10;
  height: 82.5625rem;
  width: 94%;
  max-width: 100%;
  left: 0;
  top: -6.5625rem;
  background-image: linear-gradient(to right top, transparent 49.9%, #F9F871 50%, #F9F871 calc(49.9% + 1px), transparent calc(50.1% + 1px));
}
.content__titlewrap {
  text-align: center;
}
.content__title {
  font-weight: 300;
  font-size: 1.25rem;
  letter-spacing: 2px;
}
@media screen and (min-width: 768px) {
  .content__title {
    font-size: 1.875rem;
  }
}
.content__title-text {
  font-size: 3.125rem;
  color: #f9f871;
  letter-spacing: 2px;
  z-index: -1;
  opacity: 0.5;
  font-family: "Noto Serif JP", serif;
  margin-top: -5.3125rem;
}
@media screen and (min-width: 768px) {
  .content__title-text {
    font-size: 6.875rem;
    margin-right: 41.25rem;
    transform: translateY(-5px);
  }
}
.content__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 3.75rem;
}
@media screen and (min-width: 768px) {
  .content__list {
    margin-top: -2.5rem;
    flex-wrap: nowrap;
  }
}
.content__item {
  width: 50%;
  height: 10.5rem;
  position: relative;
  transition: ease all 0.3s;
}
.content__item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.6;
  display: none;
  transition: ease all 0.3s;
}
@media screen and (min-width: 768px) {
  .content__item {
    width: 100%;
    height: 20rem;
  }
}
.content__item:hover::before {
  display: block;
}
.content__item:nth-child(1) {
  background-image: url(/images/common/contenr-image1.jpg);
  background-size: cover;
}
.content__item:nth-child(2) {
  background-image: url(/images/common/content-image2.jpg);
  background-size: cover;
}
.content__item:nth-child(3) {
  background-image: url(/images/common/content-image3.jpg);
  background-size: cover;
}
.content__item:nth-child(4) {
  background-image: url(/images/common/content-image4.jpg);
  background-size: cover;
}
.content__item-link {
  display: block;
  height: 100%;
  width: 100%;
}
.content__item-text {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
@media screen and (min-width: 768px) {
  .content__item-text {
    font-size: 1.5625rem;
  }
}